@import 'variables';

.model {
  &__stage {
    font-family: 'Computer Modern Sans';
    font-size: 24px;

    &--faded {
      opacity: 0.2;
    }
  }

  &__dimension {
    font-family: 'Computer Modern Sans';
    font-size: 22px;
  }

  &__stage-rect {
    shape-rendering: crispEdges;

    &--reporting {
      fill: $reporting;
    }

    &--analyzing {
      fill: $analyzing;
    }

    &--optimizing {
      fill: $optimizing;
    }

    &--empowering {
      fill: $empowering;
    }

    &--innovating {
      fill: $innovating;
    }
  }

  &__icon {
    opacity: 0.25;

    &--chart {
      transform: translate(-29px, 269px) scale(0.35);
    }

    &--magnify {
      transform: translate(180px, 270px) scale(0.7);
    }

    &--gear {
      transform: translate(330px, 240px) scale(0.3);
    }

    &--fist {
      transform: translate(480px, 200px) scale(7);
    }

    &--bulb {
      transform: translate(646px, 134px) scale(0.35);
    }

    path {
      fill: #fff;
    }
  }

  &__stage-name {
    font-size: 22px;
    font-family: 'Merriweather', sans-serif;
  }
}
