@import 'reset';
@import 'typography';
@import 'variables';
@import 'print';

p {
  margin: $gutter-small 0;
}

a {
  text-decoration: none;
  color: $blue-dark;
  border: 2px solid transparent;

  &:visited {
    color: $blue-dark;
  }

  &:hover {
    border-bottom: 2px solid $empowering;
  }
}

html {
  height: 100%;
}

b {
  font-weight: bold;
}

body {
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

#root {
  height: 100%;
}

.dict {
  color: $blue-dark;
  border-bottom: 1px dashed;
  cursor: pointer;

  &:hover {
    .dict__definition {
      display: block;
      position: fixed;
      top: 20px;
      background: $innovating;
      color: $white;
      padding: 10px;
      border-radius: $border-radius;
      left: 50%;
      margin-left: -150px;
      width: 300px;
      text-align: center;
    }
  }

  &__definition {
    display: none;
  }
}
