@import 'variables';

.stage-page {
  &__nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  &__nav-item {
    padding: 10px;
    flex-basis: 20%;
    text-align: center;

    &--active {
      border-bottom: 2px solid $empowering;
    }
  }

  &__action-list {
    list-style-type: disc;
  }
}
