@import 'variables';

.report {
  margin-top: 20px;

  &--submitted {
    .no-show {
      display: none;
    }
  }

  &__info {
    font-size: 18px;
  }

  &__title {
    line-height: 40px;
    font-size: 32px;
    margin-top: 20px;
  }

  &__meta {
    display: flex;
    margin: 20px 0;
    width: 600px;
    flex-direction: column;
    flex-wrap: wrap;
  }

  &__meta-row {
    display: flex;

    div {
      min-width: 200px;
    }
  }

  &__model {
    margin-top: -200px;
  }

  &__overview {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    margin: 20px 0;
  }

  &__chart {
    flex-basis: 500px;
    margin: 20px;
  }

  &__overview-score {
    flex-grow: 1;
  }

  &__buttons {
    display: flex;
    justify-content: space-between;
  }

  &__link {
    font-size: 14px;
  }

  &__download {
    font-size: 18px;
    height: 80px;
    button {
      padding: 10px 20px;
    }
    margin: 20px 0;
  }

  &__overview-dimension {
    display: flex;
    flex-direction: column;
    margin: 10px 0;
  }

  &__overview-dimension-progress {
    height: 10px;
    background: #000;
    border-radius: 2px;
    margin-bottom: 5px;

    &--level-0 {
      background: $reporting;
    }

    &--level-1 {
      background: $reporting;
    }

    &--level-2 {
      background: $analyzing;
    }

    &--level-3 {
      background: $optimizing;
    }

    &--level-4 {
      background: $empowering;
    }

    &--level-5 {
      background: $innovating;
    }
  }

  &__chart {
  }

  &__reading-guide {
    page-break-inside: avoid;
  }

  &__dimension {
    margin: 40px 0 10px 0;
    page-break-inside: avoid;
  }

  &__dimension-header {
    display: flex;
    margin: 10px 0;
    align-items: center;
  }

  &__dimension-name {
    font-weight: bold;
  }

  &__dimension-stage {
    flex-grow: 1;
    text-align: right;
  }

  &__dimension-score {
    font-size: 24px;
    flex-grow: 1;
    text-align: right;
  }

  &__dimension-progress {
    height: 20px;
    border-radius: 2px;
    background: $innovating;
    margin: 5px 0;

    &--level-1 {
      background: $reporting;
    }

    &--level-2 {
      background: $analyzing;
    }

    &--level-3 {
      background: $optimizing;
    }

    &--level-4 {
      background: $empowering;
    }

    &--level-5 {
      background: $innovating;
    }
  }

  &__action-list {
    list-style-type: disc;
    margin: 20px;
  }

  &__action-item {
    margin: 0 5px;
  }
}
