@import 'variables';

.maturity-question {
  transition: all 0.3s;
  width: 100%;
  margin: 10px 0;
  background: $blue-ultra-light;
  border-radius: $border-radius;

  &__title {
    padding: 10px;
    font-weight: bold;
  }

  &__info {
    font-weight: normal;
    font-size: 14px;
  }

  &__option {
    display: flex;
    cursor: pointer;
    transition: all 0.2s;
    padding: 5px 0;
    border-left: 4px solid transparent;

    &--highlighted {
      border-left: 4px solid $empowering;
    }

    &--answered {
      border-left: 4px solid $empowering;
      background: $blue-light;
    }

    &:hover {
      text-decoration: underline;
    }
  }

  &__index {
    flex-basis: 50px;
    padding-left: 20px;
    flex-shrink: 0;
  }

  &__description {
  }

  &.--unanswered {
    box-shadow: 0 0 10px red;
  }
}
