@import 'variables';

.introduction {
  position: relative;

  &__intro {
    margin: 20px 0;
  }

  &__button {
    margin: 20px auto;
    padding: 30px 60px;
    box-shadow: 0 10px 70px -10px rgba(0, 0, 0, 0.6);
  }

  &__name {
    font-size: 32px;
  }

  &__usps {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  &__usp {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    flex-basis: 33%;
    min-width: 200px;
    margin: 20px 0;
    padding: 0 5px;
  }

  &__usp-title {
    margin: 10px 0;
    font-size: 32px;
    text-align: center;
  }

  &__usp-text {
    text-align: center;
  }

  &__stages {
    display: flex;
  }

  &__stage {
    margin: 10px 0;
    display: flex;
  }

  &__title {
    min-height: 68px;
    width: 100%;
    font-size: 32px;
    flex-direction: row-reverse;
    display: flex;
    align-items: center;
    margin: 25px 0 10px 0;

    h2 {
      padding: 20px;
    }
  }

  &__header {
    text-align: center;
    margin-bottom: 30px;
  }

  &__section {
    margin-top: $gutter-huge;
  }

  &__section-columns {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;

    &--reverse {
      flex-direction: row-reverse;
    }
  }

  &__section-img {
    flex-basis: 380px;
    max-width: 380px;
    display: flex;
    justify-content: center;
  }

  &__copy {
    flex-basis: 380px;
    max-width: 380px;
    flex-grow: 1;
  }

  &__dimensions {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: stretch;
  }

  &__dimension {
    flex-basis: 50%;
    margin-bottom: 10px;
    text-align: center;
    display: flex;
    flex-direction: column;
  }

  &__dimension-header {
    color: $white;
    font-size: 22px;
    padding: 10px;

    &--data {
      background: $reporting;
    }

    &--metrics {
      background: $reporting;
    }

    &--skills {
      background: $analyzing;
    }

    &--technology {
      background: $analyzing;
    }

    &--leadership {
      background: $optimizing;
    }

    &--culture {
      background: $optimizing;
    }

    &--strategy {
      background: $empowering;
    }

    &--agility {
      background: $empowering;
    }

    &--integration {
      background: $innovating;
    }

    &--empowerment {
      background: $innovating;
    }
  }

  &__dimension-content {
    padding: 10px;
    flex-grow: 1;

    &--data {
      border-left: 2px solid $reporting;
      border-right: 1px solid $reporting;
      border-bottom: 2px solid $reporting;
    }

    &--metrics {
      border-left: 1px solid $reporting;
      border-right: 2px solid $reporting;
      border-bottom: 2px solid $reporting;
    }

    &--skills {
      border-left: 2px solid $analyzing;
      border-right: 1px solid $analyzing;
      border-bottom: 2px solid $analyzing;
    }

    &--technology {
      border-left: 1px solid $analyzing;
      border-right: 2px solid $analyzing;
      border-bottom: 2px solid $analyzing;
    }

    &--leadership {
      border-left: 2px solid $optimizing;
      border-right: 1px solid $optimizing;
      border-bottom: 2px solid $optimizing;
    }

    &--culture {
      border-left: 1px solid $optimizing;
      border-right: 2px solid $optimizing;
      border-bottom: 2px solid $optimizing;
    }

    &--strategy {
      border-left: 2px solid $empowering;
      border-right: 1px solid $empowering;
      border-bottom: 2px solid $empowering;
    }

    &--agility {
      border-left: 1px solid $empowering;
      border-right: 2px solid $empowering;
      border-bottom: 2px solid $empowering;
    }

    &--integration {
      border-left: 2px solid $innovating;
      border-right: 1px solid $innovating;
      border-bottom: 2px solid $innovating;
    }

    &--empowerment {
      border-left: 1px solid $innovating;
      border-right: 2px solid $innovating;
      border-bottom: 2px solid $innovating;
    }
  }
}
