.fiveten {
  display: flex;
  flex-direction: column;
  gap: 16px;
  font-family: "Noto Sans", sans-serif;
  border: none;
  padding: 48px;
  border-radius: 16px;
  font-size: 1.6rem;
  font-weight: 800;
  line-height: 1.1em;
  color: #261e48;
  text-shadow: none;
  margin: 48px 0;
  background: #ffffff;
  box-shadow: 0 10px 25px -5px rgba(38, 30, 72, 0.15), 0 8px 10px -6px rgba(38, 30, 72, 0.1), 0 4px 15px -3px rgba(117, 102, 232, 0.12), 0 0 0 1px rgba(117, 102, 232, 0.08);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}
.fiveten:hover {
  transform: translateY(-2px);
  box-shadow: 0 20px 40px -8px rgba(38, 30, 72, 0.2), 0 15px 20px -10px rgba(38, 30, 72, 0.15), 0 8px 25px -5px rgba(117, 102, 232, 0.18), 0 0 0 1px rgba(117, 102, 232, 0.12);
}
@media (min-width: 768px) {
  .fiveten {
    font-size: 2.5rem;
    padding: 64px;
  }
}
.fiveten__introducing {
  font-size: 0.6rem;
  font-weight: 800;
  color: #5c40c2;
  text-transform: uppercase;
  font-family: "Noto Sans", sans-serif;
  line-height: 100%;
}
@media (min-width: 768px) {
  .fiveten__introducing {
    font-size: 1.2rem;
  }
}
.fiveten__line {
  text-wrap: balance;
}
.fiveten__brand {
  color: #7566e8;
  font-weight: 700;
}
.fiveten__tagline {
  font-size: 1rem;
  width: fit-content;
  font-family: "Noto Sans", sans-serif;
  font-weight: 500;
  background: #7566e8;
  color: #ffffff;
  border-radius: 999px;
  line-height: 1em;
  display: inline-flex;
  padding: 16px 24px;
  letter-spacing: 0.05em;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  border: none;
  text-decoration: none;
  outline: none;
}
.fiveten__tagline:visited {
  color: #ffffff;
}
.fiveten__tagline:hover {
  background: #6a4fdc;
  text-decoration: none;
  border-bottom: none;
}
.fiveten__tagline:active {
  transform: translateY(0);
}

