@import 'variables';

.input {
  margin: 10px 0;
  display: flex;

  &--meta {
    background: $pink;
    padding: 10px;
    border-radius: 2px;
  }

  &__description {
    padding: 10px;
    flex-grow: 1;
    text-align: right;
    flex-basis: 60%;
  }

  &__field {
    flex-basis: 40%;
    padding: 10px;
    border-radius: 2px;
    border: 1px solid;
  }
}
