@import 'variables';

.app {
  min-height: 100%;
  display: flex;
  flex-direction: column;

  &__main {
    line-height: 1.41rem;
    width: 100%;
    max-width: 800px;
    padding: 0 10px;
    margin: $gutter-small auto $gutter-huge auto;
    flex-grow: 1;
  }

  &__footer {
    width: 100%;
    background: $blue-dark;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $white;
    text-align: center;
  }
}
