@import 'variables';

.header {
  display: flex;
  justify-content: space-between;
  background: $blue-dark;
  color: $white;
  padding: $gutter-tiny $gutter-large;
  align-items: center;

  &__title {
    margin: 10px 0;
    cursor: pointer;
    font-size: 28px;
    line-height: 28px;
    color: $white;

    &:visited {
      color: $white;
    }

    &:hover {
      color: $reporting;
      border-bottom: 2px solid transparent;
    }
  }

  &__nav {
    display: flex;
    flex-direction: row;
    width: 200px;
    justify-content: space-between;
  }

  &__nav-item {
    text-transform: uppercase;
    font-size: 16px;
    color: $white;

    &:visited {
      color: $white;
    }

    &:hover {
      color: $reporting;
      border-bottom: 2px solid transparent;
    }
  }
}

@media only screen and (max-width: 600px) {
  .header {
    padding: 10px;

    &__title {
      font-size: 24px;
      line-height: 24px;
    }
  }
}
