// Colors
$black: #444444;
$white: #ffffff;

$reporting: #ff7580;
$analyzing: #ffc37e;
$optimizing: #54ce98;
$empowering: #6cb5ed;
$innovating: #9d92ef;

$pink: lighten($reporting, 16%);
$pink-light: lighten($pink, 8%);

$blue-dark: darken($empowering, 16%);
$blue-light: lighten($empowering, 16%);
$blue-ultra-light: lighten($empowering, 24%);

// Gutters
$gutter-tiny: 5px;
$gutter-small: 10px;
$gutter: 20px;
$gutter-large: 25px;
$gutter-huge: 50px;

$border-radius: 4px;
