@import 'variables';

.likert {
  display: flex;
  flex-direction: column;
  background: $pink;
  padding: 10px;
  border-radius: $border-radius;
  margin: 5px 0;

  &__title {
    font-weight: bold;
  }

  &__options {
    display: flex;
    flex-wrap: wrap;
    margin: 10px 0 0 0;
    justify-content: space-between;
    align-items: stretch;
    font-size: 14px;
  }

  &__option {
    padding: 5px;
    display: flex;
    flex: 1 0 50px;
    height: 70px;
    text-align: center;
    justify-content: center;
    align-items: center;
    background: $pink-light;
    border-radius: $border-radius;
    border: 1px solid transparent;

    &:hover {
      background: $white;
      cursor: pointer;
    }

    &--selected {
      background: $white;
      border-color: $black;
    }
  }

  &.--unanswered {
    box-shadow: 0 0 10px $reporting;
  }
}
