.dimension {
  transition: all 0.5s;
  opacity: 0;

  &--entering {
    opacity: 0;
  }

  &--entered {
    opacity: 1;
  }

  &__name {
    text-align: right;
    padding: 10px 0;
    font-size: 32px;
  }

  &__description {
    border-bottom: 1px solid;
  }

  &__section {
    border-bottom: 1px solid;
    padding: 10px 0 20px 0;
  }
}
