@import 'variables';

.model-page {
  &__stage-header {
    padding: $gutter-small;
    color: $white;
    display: flex;
    align-items: center;
    min-height: 50px;
    flex-shrink: 0;
    margin: $gutter-large 0 0 0;

    &--reporting {
      background: $reporting;
    }

    &--analyzing {
      background: $analyzing;
    }

    &--optimizing {
      background: $optimizing;
    }

    &--empowering {
      background: $empowering;
    }

    &--innovating {
      background: $innovating;
    }
  }

  &__stage-content {
    border: 2px solid transparent;
    padding: 10px 20px;
    flex-grow: 1;
    display: flex;
    align-items: center;

    &--reporting {
      border-color: $reporting;
    }

    &--analyzing {
      border-color: $analyzing;
    }

    &--optimizing {
      border-color: $optimizing;
    }

    &--empowering {
      border-color: $empowering;
    }

    &--innovating {
      border-color: $innovating;
    }
  }

  &__dimension-header {
    margin: $gutter-huge 0 0 0;
  }
}
