/* latin */
@font-face {
  font-family: 'Cambo';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Cambo'), local('Cambo-Regular'),
    url(https://fonts.gstatic.com/s/cambo/v8/IFSqHeNEk8FJk719qE8.woff2)
      format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA,
    U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215,
    U+FEFF, U+FFFD;
}

h1 {
  font-size: 36px;
  font-weight: 300;
  line-height: 40px;
  margin: $gutter 0;

  &--bottom-margin {
    margin-bottom: $gutter;
  }
}

h2 {
  font-size: 28px;
  margin-bottom: 10px;
  font-weight: 300;
  line-height: 30px;
}

.h2 {
  &--margin {
    margin-top: $gutter-huge;
  }
}

h3 {
  font-size: 22px;
  font-weight: 300;
}

body {
  font-family: 'Merriweather', sans-serif;
  font-size: 16px;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $black;
}
