@import 'variables';

.button {
  background: $blue-dark;
  color: $white;
  padding: 30px 60px;
  border-radius: $border-radius;
  box-shadow: 0 10px 50px -10px rgba(0, 0, 0, 0.6);
  transition: all 0.2s;
  font-family: 'Merriweather', sans-serif;
  outline: none;
  cursor: pointer;
  min-width: 50%;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  border: none;

  &:hover {
    box-shadow: none;
    border: none;
    margin-top: -2px;
    margin-bottom: 2px;
    color: $white;
  }

  &:visited {
    color: $white;
  }

  &--block {
    display: flex;
    width: 100%;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: $gutter-large auto $gutter-huge auto;
  }
}
