@import 'variables';

.assessment {
  position: relative;

  &__menu {
    display: flex;
    justify-content: space-between;
    margin: 0 0 10px 0;
  }

  &__menu-item {
    display: flex;
    font-size: 16px;
    font-weight: bold;
    flex-grow: 1;
    justify-content: center;
    padding: 5px;

    &--past {
      opacity: 0.5;
    }

    &--current {
      text-decoration: underline;
    }
  }

  &__progress {
    height: 20px;
    background: $empowering;
    border-radius: 2px;
    transition: all 0.4s;
    margin: 10px 0;
  }

  &__dimension {
    font-weight: bold;
    text-align: right;
  }
}
